import React from "react";
import { Button, Col, Form, Input, Row, Select } from "antd"
import { useDispatch } from "react-redux";
import { addUser } from "../../redux/action/user";

interface IFormAdd {
	onOk: Function,
	onCancel: Function
}

const { Option } = Select;

export const UserAdd = ({ onOk, onCancel }: IFormAdd): JSX.Element => {
	const dispatch: Function = useDispatch();
	const [form] = Form.useForm();

	const onFinish = (values: any) => {
		onOk();
		dispatch(addUser(values));
		form.resetFields();
		alert('  ');
	};

	const onFinishFailed = (errorInfo: any) => {
		alert(errorInfo.errorFields[0].errors[0])
		console.log('Failed:', errorInfo);
	};


	return (
		<Form
			className="form"
			form={form}
			layout="vertical"
			initialValues={{ remember: true }}
			onFinish={onFinish}
			onFinishFailed={onFinishFailed}
		>
			<Row gutter={24}>
				<Col span={12}>
					<Form.Item
						label=""
						name="fullName"
						rules={[
							{ required: true, message: ' ,  !' },
							{ min: 3, message: '     3 ', },
						]}
					>
						<Input />
					</Form.Item>
				</Col>
				<Col span={12}>
					<Form.Item
						label=""
						name="group"
						rules={[
							{ required: true, message: ' ,  !' },
						]}
					>
						<Select>
							<Option value=" "> </Option>
							<Option value=" HTP"> HTP</Option>
							<Option value=""></Option>
							<Option value=" "> </Option>
							<Option value=""></Option>
						</Select>
					</Form.Item>
				</Col>
			</Row>
			<Row gutter={24}>
				<Col span={12}>
					<Form.Item
						label=" "
						name="organization"
						rules={[
							{ required: true, message: ' ,   !' },
							{ min: 3, message: '     5 ', },
						]}
					>
						<Input />
					</Form.Item>
				</Col>
				<Col span={12}>
					<Form.Item
						label=" "
						name="phone"
						rules={[
							{ required: true, message: ' ,    !' },
							{ pattern: /^\+380\d{9}$/, message: ' ,    ', },
						]}
					>
						<Input defaultValue={'+380'} maxLength={13} />
					</Form.Item>
				</Col>
			</Row>
			<Row gutter={24}>
				<Col span={12}>
					<Form.Item
						label=" "
						name="email"
						rules={[{ required: true, message: ' ,   !' }]}
					>
						<Input placeholder="email@gmail.com" />
					</Form.Item>
				</Col>
			</Row>
			<Form.Item className="form__footer">
				<Button className="button white" key="back" onClick={() => onCancel()}>
					Return
				</Button>
				<Button className="button" htmlType="submit" key="submit" type="primary">
					Submit
				</Button>
			</Form.Item>
		</Form >
	)
}